.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.modal__backdrop {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(130, 130, 130, 0.70);
  opacity: 0;
}

.modal__backdrop--visible {
  opacity: 1;
}



