  text-decoration: none;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0 10px;
  font-size: 13px;
  background: rgba(255, 255, 255, 1.00);
  letter-spacing: 1px;
  -webkit-transition-duration: .7s;
       -o-transition-duration: .7s;
          transition-duration: .7s;
