
.item-carousel-wrapper {  /* container */
  width: 100vw;
  height: 140px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  position: relative;
}

#item-carousel {  /* carousel */
  width: 100%;
  height: 100%;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

#item-carousel figure {
  margin: 0;
  display: block;
  position: absolute;
  width: 100vw;
  height: 116px;
  border: 2px solid black;
}



