.promotion-bar {
  color: #fff;
  position: absolute;
  height: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  background: rgba(0, 0, 0, 0.35);  
  background-position: center center;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 3;
  top: 7.5vh;
  left: 0;
  right: 0;
  -ms-flex-align: center;
      align-items: center;
}

.promotion-message {
  color: rgba(255, 255, 255, 1);
  font-family: 'OratorStd', sans-serif;
  font-size: 12px;
  text-transform: lowercase;
  letter-spacing: 1px;
}

.promotion-bar-close {
  margin-right: 10px;
  height: 40px;
  right: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
  position: absolute;
  display: -ms-flexbox;
  display: flex;  
  -ms-flex-pack: center;  
      justify-content: center; 
  -ms-flex-align: center; 
      align-items: center;
}

.material-icons#close {
  cursor: pointer;
  color: #fff;
  opacity: 1;  
  font-size: 24px;
  padding: 5px;
  margin: 0 5px;
  border-radius: 50%;
  -webkit-transition-property: opacity;
  -o-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 500ms;
       -o-transition-duration: 500ms;
          transition-duration: 500ms;
}

.material-icons#close:hover {
  opacity: 0.4;
  -webkit-transition-property: opacity;
  -o-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 500ms;
       -o-transition-duration: 500ms;
          transition-duration: 500ms;
}


